*{
    box-sizing: border-box;
}

body{
    background-color: #E9EAEC;
}

.bgr{
  background-image: url("./logobg.png");
}


@font-face {
    font-family: thermite;
    src: url(./FontsFree-Net-Thermite.ttf);
}


.thermite{
    font-family: thermite;
}

.cus::before {
    content: "";
    height: 40px;
    width: 40px;
    background-color: transparent;
    position: absolute;
    left: -40px;
    top:0;
    border-top-right-radius: 50px;
    box-shadow: 10px -10px 0 10px #E9EAEC;
    
}

.cus::after {
    content: "";
    height: 40px;
    width: 40px;
    background-color: transparent;
    position: absolute;
    top:0;
    right: -40px;
    border-top-left-radius: 50px;
    box-shadow: -10px -10px 0 10px #E9EAEC;
}



.cus{
    font-family: thermite;
}

.screen{
    mix-blend-mode: screen;
}

.colorburn{
    mix-blend-mode:difference;
}

.hero {
    background: rgba(0, 0, 0, 0.5) url("./hro.jpg") no-repeat center / cover;
}


a {
    cursor: pointer;
    position: relative;
    display: inline-block;
    letter-spacing: 0.05rem;
}


a:after{
    content: "";
    background-color: red;
    width: 0;
    height: 2px;
    position: absolute;
    border-radius: 50px;
    left: calc(34%); 
    bottom: -2px ;
    opacity: 0.9;
    transition: 0.1s;

}

a:hover:after,
a.selected::after {
    width: 30%;
}

.sticky{
    position: fixed;
    transition: all 0.3s ease-out;
}



.cus2::before {
    box-shadow: 10px -10px 0 10px #171717;
    transition: all 0.3s ease-out;
  }
  
  .cus2::after {
    box-shadow: -10px -10px 0 10px #171717;
    transition: all 0.3s ease-out;
  }

